import Swal from 'sweetalert2'
import { quickReplyHtml } from './quickReplyHtml'
import { sweetAlertCommonParams } from '../../utils/functions'
import { lang } from '../../assets/lang'

export const quickReplyModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: lang.quick_reply_modal_title,
    html: quickReplyHtml,

    willOpen: () => {
      $('#quick-reply-button-text').val(oldData.buttonText || '')
    },

    preConfirm: () => {
      const buttonTextValue = $('#quick-reply-button-text').val()
      
      if (! buttonTextValue) {
        return Swal.showValidationMessage(lang.quick_reply_modal_msg_button_text_required)
      }
      
      if (buttonTextValue.length > 20) {
        return Swal.showValidationMessage(lang.quick_reply_modal_msg_button_text_length)
      }

      return {
        buttonText: buttonTextValue
      }
    },    
  })
}