import { sectionModal } from './sectionModal'
import { updateNodeConnections } from '../../utils/functions'

// eslint-disable-next-line
export default async (node, editor) => {
  const oldData = node.data
  const { value: sectionData } = await sectionModal(oldData, node)

  if (sectionData && Object.values(sectionData).length > 0) {
    Object.assign(node.data, sectionData)

    node.update()
    updateNodeConnections(node)
  }

  return false
}