import cleanXss from 'xss'
import Swal from 'sweetalert2'
import { nanoid } from 'nanoid/async'
import { lang } from '../../assets/lang'
import { actionButtonHtml } from './actionButtonHtml'

import {
  makeAjaxCall,
  uploadFileViaDropzone,
  sweetAlertCommonParams,
  selectedOptionsTextArray,
  callFunctionWithInterval,
  createSelect2DynamicOption,
  getSelectOptionsFromArrayOfObjects,
  removeSpinner,
  appendSpinner,
  mimeTypesMap,
  isPlainObject,
} from '../../utils/functions'

let broadcastDropzone
let scriptGlobalOldData
let isFilesAddedToHead = false
let botTemplateResponseArray = []
let postbackListResponseData = []
const ajaxCallStates = {
  hasLabelData: false,
  hasBotTemplateData: false,
  hasPostbackData: false,
}
var whatsapp_bot_id = window.xitFlowBuilderData.whatsapp_bot_id
var builder_table_id = window.xitFlowBuilderData.builder_table_id

// eslint-disable-next-line
export const actionButtonModal = async (oldData, node, defaultData) => {
  scriptGlobalOldData = oldData
  const commonParams = sweetAlertCommonParams(node, '22rem')
  
  return await Swal.fire({
    ...commonParams,
    title: lang.action_button_modal_title,
    html: actionButtonHtml(oldData, node, defaultData),

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })

      if (false === isFilesAddedToHead) {
        $('head').append('<script src="https://cdn.jsdelivr.net/npm/bootstrap-colorpicker@3.4.0/dist/js/bootstrap-colorpicker.min.js"></script>')
        $('head').append('<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap-colorpicker@3.4.0/dist/css/bootstrap-colorpicker.min.css">')

        $('head').append('<script src="https://cdnjs.cloudflare.com/ajax/libs/jquery-datetimepicker/2.4.3/jquery.datetimepicker.min.js"></script>')
        $('head').append('<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/jquery-datetimepicker/2.4.3/jquery.datetimepicker.min.css">')

        isFilesAddedToHead = true
      }

      /** Handles action buttons list section */
      const actionButtonActionGroupElement = $('#action-button-actions-group')
      const actionButtonElement = $('#action-button-actions')
      const broadcastingGroupElement = $('#action-button-broadcasting-group')

      const campaignNameElement = $('#campaign-name')
      const botTemplateInputElement = $('#bot-template-input')
      const botTemplateHeaderMediaUrlInputElement = $('#bot-template-header-media-url-input')
      const labelIdsElement = $("#label-ids")
      const includeLabelIdsElement = $('#include-label-ids')
      const excludeLabelIdsElement = $('#exclude-label-ids')

      createSelect2DynamicOption(labelIdsElement)

      const sendingTimeElement = $('#sending-time')
      const scheduleTimeElement = $('#schedule-time')
      const timezoneElement = $('#timezone')

      let broadcastingType = oldData.broadcastingType || 'hours24'

      broadcastingGroupElement.attr('style','display:none !important')
      appendSpinner(actionButtonActionGroupElement, true)

      // Handles loading labels
      const labelIdsAjaxData = {
        url: window.xitFlowBuilderData.routes.flow_builder_get_label_dropdown,
        data: { 
          whatsapp_bot_id,
        }
      }

      makeAjaxCall(function(response) {
        ajaxCallStates.hasLabelData = true

        const labelOptions = getSelectOptionsFromArrayOfObjects(response)

        labelIdsElement.html(labelOptions)        
        includeLabelIdsElement.html(labelOptions)
        excludeLabelIdsElement.html(labelOptions)

        labelIdsElement.val(oldData.labelIds || '').trigger('change')
        includeLabelIdsElement.val(oldData.telegram_bot_label_ids || '').trigger('change')
        excludeLabelIdsElement.val(oldData.excluded_telegram_bot_label_ids || '').trigger('change')
      }, labelIdsAjaxData) 
      
      // Handles loading bot templates
      const botTemplateAjaxData = {
        method: 'GET',
        url: window.xitFlowBuilderData.routes.get_bot_templates,
      }
    
      makeAjaxCall(function(response) {
        ajaxCallStates.hasBotTemplateData = true
        botTemplateResponseArray = response

        const botTemplateSelectOptions = prepareSelectOptions(response)
        botTemplateInputElement.html(botTemplateSelectOptions)
        botTemplateInputElement.val(oldData.botTemplateId || '').trigger('change')
      }, botTemplateAjaxData)

      // Handles loading postback list
      const postbackListAjaxData = {
        url: window.xitFlowBuilderData.routes.flow_builder_get_dropdown_postback,
        data: {
          whatsapp_bot_id
        }
      }
    
      makeAjaxCall(function(response) {
        ajaxCallStates.hasPostbackData = true
        postbackListResponseData = response.dropdown_str
      }, postbackListAjaxData)

      callFunctionWithInterval(() => {
        if (ajaxCallStates.hasLabelData && 
          ajaxCallStates.hasBotTemplateData &&
          ajaxCallStates.hasPostbackData
        ) {
          broadcastingGroupElement.removeAttr('style')
          removeSpinner(actionButtonActionGroupElement)
          bootstrap()

          return true
        }

        return false
      }, 50)

      function bootstrap() {
        // Populate old values
        $('#chatbox-bg-color').val(oldData.chatboxBgColor || '#00A884')
        $('#chatbox-text-color').val(oldData.chatboxTextColor || '#FFFFFF')
        $('#chatbox-display-name').val(oldData.chatboxDisplayName || lang.action_button_html_chatbox_display_name_placeholder_whatsapp)
        $('#chatbox-welcome-message').val(oldData.chatboxWelcomeMessage || lang.action_button_html_chatbox_welcome_message_placeholder)
        $('#chatbox-prefill-message').val(oldData.chatboxPrefillMessage || lang.action_button_html_chatbox_prefill_message_placeholder)
        $('input[name="chatbox-position"][value="' + oldData.chatboxPosition + '"]').prop('checked', true)
        $('input[name="chatbox-visibility"][value="' + oldData.chatboxVisibility + '"]').prop('checked', true)
        $('#chatbox-position-offset-x').val(oldData.xOffset || '10')
        $('#chatbox-position-offset-y').val(oldData.yOffset || '10')

        $('#button-text').val(oldData.buttonText || lang.action_button_html_button_text_placeholder)
        $('#button-bg-color').val(oldData.buttonBgColor || '#00A884')
        $('#button-text-color').val(oldData.buttonTextColor || '#ffffff')
        $('#button-hover-bg-color').val(oldData.buttonHoverBgColor || '#128C7E')
        $('#button-hover-text-color').val(oldData.buttonHoverTextColor || '#fffddd')
        $('input[name="button-size"][value="' + oldData.buttonSize + '"]').prop('checked', true)
        $('#reference').val(oldData.reference || '')

        campaignNameElement.val(oldData.campaign_name || '')
        $('input[name="broadcasting-type"][value="' + broadcastingType + '"]').prop('checked', true)
        
        includeLabelIdsElement.val(oldData.telegram_bot_label_ids || '').trigger('change')
        excludeLabelIdsElement.val(oldData.excluded_telegram_bot_label_ids || '').trigger('change')

        scheduleTimeElement.val(oldData.schedule_time || '')
        timezoneElement.val(oldData.timezone || '').trigger('change')

        const oldSelectedActionButton = oldData.actionType 
          || ((defaultData && defaultData.selectedActionButton) 
            ? defaultData.selectedActionButton
            : '')

        actionButtonElement.val(oldSelectedActionButton || '').trigger('change')
        sendingTimeElement.prop('checked', ('later' === oldData.schedule_type) ? true : false)

        let includedLabels = oldData.telegram_bot_label_ids ? oldData.telegram_bot_label_ids : [];
        let excludedLabels = oldData.excluded_telegram_bot_label_ids ? oldData.excluded_telegram_bot_label_ids : [];

        function loadAndDisplayBotSubscriberNumber() {
          const countBotSubscriberAjaxData = {
            url: window.xitFlowBuilderData.routes.count_bot_subscriber_based_on_labels,
            data: { 
              whatsapp_bot_id,
              included_labels: includedLabels,
              excluded_labels: excludedLabels,
              broadcast_type: broadcastingType,
            }
          }

          makeAjaxCall(function(response) {
            if (Array.isArray(response)) {
              const subscribers = response[0].subscribers;
              window.xitFlowBuilderGlobalValue_BotSubscibers = subscribers;
              $('.targeted-subscribers').html(subscribers)
            }
          }, countBotSubscriberAjaxData)
        }

        loadAndDisplayBotSubscriberNumber()

        toggleBroadcastingTypeAndOthers(broadcastingType)

        $('input[name="broadcasting-type"]').on('change', (event) => {
          const selectedType = event.target.value
          broadcastingType = selectedType
          toggleBroadcastingTypeAndOthers(selectedType)
          loadAndDisplayBotSubscriberNumber()
        })
        
        $(botTemplateInputElement).on('change', function() {
          handleUploadAndButtonFieldsOnTemplateSelection(
            broadcastingType,
            $(this).val(),
            $(this).find(':selected').data('header-type'),
            $(this).find(':selected').data('header-subtype'),
            $(this).find(':selected').data('button-type')
          )
        })

        const selectedActionButton = actionButtonElement.val()
        toggleActionButtonsList(selectedActionButton)

        actionButtonElement.on('change', function() {      
          toggleActionButtonsList(this.value)
        })

        includeLabelIdsElement.on('change', function() {
          includedLabels = $(this).val();
          loadAndDisplayBotSubscriberNumber()
        })

        excludeLabelIdsElement.on('change', function() {
          excludedLabels = $(this).val();
          loadAndDisplayBotSubscriberNumber()
        })

        sendingTimeElement.on('change', function() {
          toggleSendLaterOptions(broadcastingType, this.checked)
        })      

        // Handles old data of Delay
        handleOldDataOfDelay()

        $(document).ready(function() {
          callDatetimePickerWhenItIsAvailable()
          callColorPickerWhenItIsAvailable()
          $('[data-toggle="tooltip"]').tooltip()
        })
      } // Bootstrap function ends

      // Handles uploading files
      broadcastDropzone = uploadFileViaDropzone({ 
        elementForInsertingUrlOntoInput: botTemplateHeaderMediaUrlInputElement 
      })
    },

    preConfirm: async () => {
      const actionType = $('#action-button-actions').val()
      const actionTypeText = $('#action-button-actions option:selected').text()

      const chatboxBgColor = $('#chatbox-bg-color').val()
      const chatboxTextColor = $('#chatbox-text-color').val()
      const chatboxDisplayName = $('#chatbox-display-name').val()
      const chatboxWelcomeMessage = $('#chatbox-welcome-message').val()
      const chatboxPrefillMessage = $('#chatbox-prefill-message').val()
      const chatboxPosition = $('input[name="chatbox-position"]:checked').val()
      const xOffset = $('#chatbox-position-offset-x').val()
      const yOffset = $('#chatbox-position-offset-y').val()

      const buttonText = $('#button-text').val()
      const buttonBgColor = $('#button-bg-color').val()
      const buttonTextColor = $('#button-text-color').val()
      const buttonHoverBgColor = $('#button-hover-bg-color').val()
      const buttonHoverTextColor = $('#button-hover-text-color').val()
      const buttonSize = $('input[name="button-size"]:checked').val()
      const chatboxVisibility = $('input[name="chatbox-visibility"]:checked').val()

      const reference = $('#reference').val()

      const labelIds = $('#label-ids').val()
      const labelIdsTexts = $('#label-ids option:selected')
      const labelIdTextsArray = selectedOptionsTextArray(labelIdsTexts)

      const campaign_name = $('#campaign-name').val()
      const broadcastingType = $('input[name="broadcasting-type"]:checked').val()
      const botTemplateId = $('#bot-template-input').val() || null
      const botTemplateText = $('#bot-template-input option:selected').text() || null
      const botTemplateHeaderType = $('#bot-template-input option:selected').data('header-type') || null
      const botTemplateButtonType = $('#bot-template-input option:selected').data('button-type') || null
      const botTemplateHeaderMediaUrl = $('#bot-template-header-media-url-input').val() || null

      const botTemplateQuickreplyButtonsElement = $('.bot-template-quickreply-buttons')
      const botTemplateQuickreplyButtonInputLabels = []
      const botTemplateQuickreplyButtonValues = botTemplateQuickreplyButtonsElement.map((index, element) => { // eslint-disable-line
        if (new Boolean(element.value).valueOf() === false) {
          botTemplateQuickreplyButtonInputLabels.push(cleanXss($(element).prev().text()))
        }
        return element.value
      }).get()

      const botTemplateDynamicVariableElement = $('.bot-template-dynamic-variable')
      const botTemplateDynamicVariableValues = botTemplateDynamicVariableElement.map((index, element) => { // eslint-disable-line
        const label = cleanXss($(element).prev().text())
        const position = $(element).data('position')
        return { label, value: element.value, position }
      }).get()

      const telegram_bot_label_ids = $('#include-label-ids').val()
      const includeLabelIdsText = $('#include-label-ids option:selected')
      const telegram_bot_label_ids_names = selectedOptionsTextArray(includeLabelIdsText)

      const excluded_telegram_bot_label_ids = $('#exclude-label-ids').val()
      const excludeLabelIdsText = $('#exclude-label-ids option:selected')
      const excluded_telegram_bot_label_ids_names = selectedOptionsTextArray(excludeLabelIdsText)

      const schedule_type = $('#sending-time').prop('checked')
      const schedule_time = $('#schedule-time').val()
      const	timezone = $('#timezone').val()

      if (! actionType) {
        return Swal.showValidationMessage(lang.action_button_modal_msg_action_type)
      }

      if ('messenger_engagement_plugin' === actionType) {

        if (! chatboxBgColor || ! chatboxPosition) {
          return Swal.showValidationMessage(lang.action_button_modal_msg_chatbox_attributes)
        }

        if (! buttonText) {
          return Swal.showValidationMessage(lang.action_button_modal_msg_button_text)
        }
  
        if (! buttonBgColor 
          || ! buttonTextColor 
          || ! buttonHoverBgColor 
          || ! buttonHoverTextColor
        ) {
          return Swal.showValidationMessage(lang.action_button_modal_msg_button_attributes)
        }

        if (! buttonSize) {
          return Swal.showValidationMessage(lang.action_button_modal_msg_button_size)
        }
        
        if (! reference) {
          return Swal.showValidationMessage(lang.action_button_modal_msg_reference)
        }

        const regex = /[^a-zA-Z0-9]/
        
        if (regex.test(reference)) {
          return Swal.showValidationMessage(lang.action_button_modal_msg_filter_reference_name)
        }

        const result = await fetch(window.xitFlowBuilderData.routes.flow_builder_check_reference_field_value_exists + '/' + builder_table_id + '/' + reference)
          .then(function(response) {
            return response.json()
          })

        if (false === result.status) {
          return Swal.showValidationMessage(result.message)
        }
      }

      if ('broadcasting' === actionType) {
        if (! campaign_name) {
          return Swal.showValidationMessage(lang.broadcasting_msg_campaign_name)
        }
  
        if ('anytime' === broadcastingType) {
          if (! botTemplateId) {
            return Swal.showValidationMessage(lang.broadcasting_msg_bot_template)
          }

          if ('quick_reply' === botTemplateButtonType) {
            if (botTemplateQuickreplyButtonsElement.length !== botTemplateQuickreplyButtonValues.filter(Boolean).length) {
              const message = lang.broadcasting_msg_quickreply_buttons_required + ' ' + botTemplateQuickreplyButtonInputLabels.join(',')
              return Swal.showValidationMessage(message)
            }
          }
          
          const emptyDynamicVariableFields = botTemplateDynamicVariableValues
            .filter(item => new Boolean(item.value).valueOf() === false)

          if ((botTemplateDynamicVariableElement.length ===
            botTemplateDynamicVariableValues.length) &&
            emptyDynamicVariableFields.length > 0
          ) {
            const message = lang.broadcasting_msg_quickreply_buttons_required + ' ' + emptyDynamicVariableFields.map(item => item.label).join(',')
            return Swal.showValidationMessage(message)
          }

          if ('media' === botTemplateHeaderType) {
            if (! botTemplateHeaderMediaUrl) {
              return Swal.showValidationMessage(lang.broadcasting_msg_upload_or_inser_file_url)
            }
          }
        }

        if (schedule_type) {
          if (! schedule_time) {
            return Swal.showValidationMessage(lang.broadcasting_msg_schedule_time)
          }
  
          if (! timezone) {
            return Swal.showValidationMessage(lang.broadcasting_msg_timezone)
          }
        }

        if (window.xitFlowBuilderGlobalValue_BotSubscibers < 1) {
          return Swal.showValidationMessage(lang.broadcasting_msg_targeted_subscribers)
        }
      }

      const delayData = handleDataOfDelay()

      return {
        actionType,
        actionTypeText,
        
        chatboxBgColor,
        chatboxTextColor,
        chatboxDisplayName,
        chatboxWelcomeMessage,
        chatboxPrefillMessage,
        chatboxPosition,
        chatboxVisibility,
        xOffset,
        yOffset,
        ...delayData,

        buttonText,
        buttonBgColor,
        buttonTextColor,
        buttonHoverBgColor,
        buttonHoverTextColor,
        buttonSize,
        reference,

        labelIds,
        labelIdTextsArray,

        campaign_name,

        broadcastingType,
        botTemplateId,
        botTemplateText,
        botTemplateHeaderType,
        botTemplateButtonType,
        botTemplateHeaderMediaUrl,
        botTemplateQuickreplyButtonValues,
        botTemplateDynamicVariableValues,

        telegram_bot_label_ids,
        telegram_bot_label_ids_names,

        excluded_telegram_bot_label_ids,
        excluded_telegram_bot_label_ids_names,

        schedule_type: (true === schedule_type) ? 'later' : 'now',
        schedule_time,
        timezone,
      }
    },    
  })
}

const toggleActionButtonsList = (selectedActionButton) => {
  const messengerEngagementPluginsGroupElement = $('#action-button-messenger-engagement-plugins-group')
  const broadcastingGroupElement = $('#action-button-broadcasting-group')
  
  if ('messenger_engagement_plugin' === selectedActionButton) {
    broadcastingGroupElement.removeClass('d-block').addClass('d-none')
    messengerEngagementPluginsGroupElement.removeClass('d-none').addClass('d-block')
  } else if ('broadcasting' === selectedActionButton) {
    messengerEngagementPluginsGroupElement.removeClass('d-block').addClass('d-none')
    broadcastingGroupElement.removeClass('d-none').addClass('d-block')
  } else {
    messengerEngagementPluginsGroupElement
      .add(broadcastingGroupElement)
      .removeClass('d-block').addClass('d-none')
  }
}

const toggleBroadcastingTypeAndOthers = (broadcastingType) => {
  const botTemplateWrapperElement = $('#bot-template-wrapper')
  const broadcastingSendTypeElement = $('#broadcasting-send-type')
  const sendingTimeElement = $('#sending-time')

  if ('hours24' === broadcastingType) {
    botTemplateWrapperElement.removeClass('d-block').addClass('d-none')
    broadcastingSendTypeElement.removeClass('d-block').addClass('d-none')
    handleUploadAndButtonFieldsOnTemplateSelection(
      broadcastingType, null, null, null, null
    )
    toggleSendLaterOptions(
      broadcastingType,
      sendingTimeElement.prop('checked')
    )
  } else {
    botTemplateWrapperElement.removeClass('d-none').addClass('d-block')
    broadcastingSendTypeElement.removeClass('d-none').addClass('d-block')
    handleUploadAndButtonFieldsOnTemplateSelection(
      broadcastingType,
      $('#bot-template-input').val(),
      $('#bot-template-input').find(':selected').data('header-type'),
      $('#bot-template-input').find(':selected').data('header-subtype'),
      $('#bot-template-input').find(':selected').data('button-type')
    )
    toggleSendLaterOptions(
      broadcastingType,
      sendingTimeElement.prop('checked')
    )
  }
}

const handleUploadAndButtonFieldsOnTemplateSelection = (
  broadcastingType, 
  templateId,
  headerType, 
  headerSubtype, 
  buttonType,
) => {
  const botTemplateQuickreplyButtonElement = $('#bot-template-quickreply-button-wrapper')
  const botTemplateDynamicVariableElement = $('#bot-template-dynamic-variable-wrapper')
  const botTemplateHeaderMediaElement = $('#bot-template-header-media-wrapper')
  const botTemplateHeaderMediaUrlElement = $('#bot-template-header-media-url-input')

  if ('anytime' === broadcastingType && 'media' === headerType) {
    if (mimeTypesMap[headerSubtype]) {
      const acceptedFiles = mimeTypesMap[headerSubtype].join(',')
      $(broadcastDropzone.element).find('[type="file"]').attr('accept', acceptedFiles)
      broadcastDropzone.options.acceptedFiles = acceptedFiles
    } else {
      $(broadcastDropzone.element).find('[type="file"]').attr('accept', 'unknown')
      broadcastDropzone.options.acceptedFiles = 'unknown'
    }

    botTemplateHeaderMediaUrlElement.val(scriptGlobalOldData.botTemplateHeaderMediaUrl || '')
    botTemplateHeaderMediaElement.removeClass('d-none').addClass('d-block')
  } else {
    botTemplateHeaderMediaElement.removeClass('d-block').addClass('d-none')
    $(broadcastDropzone.element).find('[type="file"]').attr('accept', 'unknown')
    broadcastDropzone.options.acceptedFiles = 'unknown'
  }

  if ('anytime' === broadcastingType && 'quick_reply' === buttonType) {
    manageButtonFieldsWithPostbackList(getButtonList(templateId))
    botTemplateQuickreplyButtonElement.removeClass('d-none').addClass('d-block')
  } else {
    botTemplateQuickreplyButtonElement.removeClass('d-block').addClass('d-none')
  }

  if ('anytime' === broadcastingType) {
    manageDynamicVariableFieldsWithValues(getDynamicVariableList(templateId))
    botTemplateDynamicVariableElement.removeClass('d-none').addClass('d-block')
  } else {
    botTemplateDynamicVariableElement.removeClass('d-block').addClass('d-none')
  }
}

const getButtonList = (templateId) => {
  const template = botTemplateResponseArray.find(item => parseInt(item.id, 10) === parseInt(templateId, 10))
  if(template && template.button_content) {
    const content = JSON.parse(template.button_content || '{}')
    if (content && 'buttons' === content.type && Array.isArray(content.buttons)) {
      return content.buttons
    }
  }

  return []
}

const manageButtonFieldsWithPostbackList = async (buttons) => {
  const botTemplateQuickreplyButtonElement = $('#bot-template-quickreply-button-wrapper')
  botTemplateQuickreplyButtonElement.html('')
  let index = 0

  for (const button of buttons) {
    const uniqueId = await nanoid(15)
    const sanitizedButtonText = cleanXss(button.text)

    const html = '' + 
      '<div class="form-group pb-3">' +
        '<label class="pb-1 control-label">' + sanitizedButtonText + '</label>' +
        '<select id="bot-template-quickreply-buttons-' + uniqueId + '" class="form-control select2 bot-template-quickreply-buttons">' + postbackListResponseData + '</select>' +
        '<script type="text/javascript">$("#bot-template-quickreply-buttons-' + uniqueId + '").select2({width: "100%",dropdownParent: $("#swal2-content")})</script>' +
      '</div>'

    botTemplateQuickreplyButtonElement.append(html).promise().done(() => {
      let previousValue = ''
      if (scriptGlobalOldData.botTemplateQuickreplyButtonValues && 
        scriptGlobalOldData.botTemplateQuickreplyButtonValues[index]
      ) {
        previousValue = scriptGlobalOldData.botTemplateQuickreplyButtonValues[index]
      }
      $('#bot-template-quickreply-buttons-' + uniqueId).val(previousValue).trigger('change')
    })

    index++
  }
}

const getDynamicVariableList = (templateId) => {
  const content = botTemplateResponseArray.find(item => parseInt(item.id, 10) === parseInt(templateId, 10))
  if(content && content.variable_map) {
    const parsedContent = JSON.parse(content.variable_map || '{}')
    if (parsedContent && parsedContent.body && isPlainObject(parsedContent.body)) {
      return Object.values(parsedContent.body)
    }
  }

  return []
}

const manageDynamicVariableFieldsWithValues = async (variables) => {
  const botTemplateDynamicVariableElement = $('#bot-template-dynamic-variable-wrapper')
  botTemplateDynamicVariableElement.html('')
  let position = 0

  for (const variable of variables) {
    position++
    
    if (! variable.startsWith('#!') && ! variable.endsWith('!#')) {
      continue
    } 

    const sanitizedVariableText = cleanXss(variable)
    const trimmedVariableText = sanitizedVariableText.replace(/[#|!]/gm, '')

    let previousValue = ''
    if (scriptGlobalOldData.botTemplateDynamicVariableValues) {
      const found = scriptGlobalOldData.
        botTemplateDynamicVariableValues.find(item => item.label === variable)
      previousValue = found ? found.value : ''
    }

    const html = '' + 
      '<div class="form-group pb-3">' +
        '<label class="pb-1 control-label">' + trimmedVariableText + '</label>' +
        '<input type="text" class="form-control bot-template-dynamic-variable" data-position="' + position + '" value="' + previousValue + '">' +
      '</div>'

    botTemplateDynamicVariableElement.append(html)
  }
}

const handleOldDataOfDelay = () => {
  const initDelayElement = $('#init-delay')
  const delayTextElement = $('#delay-delay-text')
  const initDelay = parseInt(scriptGlobalOldData.initDelay, 10) || 0

  initDelayElement.val(initDelay)
  delayTextElement.text(initDelay + ' ' + lang.delay_html_info_sec)

  initDelayElement.on('change', function(e) {
    const currentValue = e.target.value
    delayTextElement.text(currentValue + ' ' + lang.delay_html_info_sec)
  })
}

const handleDataOfDelay = () => {
  const initDelay = $('#init-delay').val()
  if (parseInt(initDelay, 10) < 0 || parseInt(initDelay, 10) > 60) {
    return Swal.showValidationMessage(lang.delay_html_msg_delay_range)
  }

  return {
    initDelay
  }
}

function toggleSendLaterOptions(broadcastingType, isSendingTimeChecked) {
  const scheduleTimeGroupElement = $('#schedule-time-group')
  if ('anytime' === broadcastingType && isSendingTimeChecked) {
    scheduleTimeGroupElement.removeClass('d-none').addClass('d-block')
  } else {
    scheduleTimeGroupElement.removeClass('d-block').addClass('d-none')
  }
}

const callDatetimePickerWhenItIsAvailable = () => {
  callFunctionWithInterval(() => {
    const targetEelement = $('#schedule-time')
    if (typeof targetEelement.datetimepicker === 'function') {
      const today = new Date()
      targetEelement.datetimepicker({
        theme:'light',
        format:'Y-m-d H:i:s',
        formatDate:'Y-m-d H:i:s',
        minDate: today
      })

      return true
    }

    return false
  })
}

const callColorPickerWhenItIsAvailable = () => {
  callFunctionWithInterval(() => {
    const targetEelement = $('.color-picker-rgb')
    if (typeof targetEelement.colorpicker === 'function') {
      targetEelement.colorpicker({
        format: 'hex',
        popover: {
          placement: 'left'
        },
      })

      return true
    }
    
    return false
  })
}

const prepareSelectOptions = (optionsArray, defaultValue = '') => {
  let html = ''

  for (const option of optionsArray) {
    if ('object' === typeof option) {
      const dataTypes = '' +
        'data-header-type="' + option.header_type + '"' +
        'data-header-subtype="' + option.header_subtype + '"' +
        'data-button-type="' + option.button_type + '"'

      const label = option.template_name + ' (' + option.header_subtype + ')'

      if (defaultValue === option.key) {
        html += '<option value="' + option.id + '" ' + dataTypes + ' selected>' + label + '</option>'
      } else {
        html += '<option value="' + option.id + '" ' + dataTypes + ' >' + label + '</option>'
      }
    }
  }

  return html
}