import CONST_SOCKET from '../const/socket'
import CONST_COMPONENT from '../const/component'
import CONST_LINGUAL_NAME from '../const/lingualName'

import { TdotmeComponent } from '../modules/default/TdotmeComponent'
import { BroadcastComponent } from '../modules/default/BroadcastComponent'
import { UserInputFlowComponent } from '../modules/default/UserInputFlowComponent'

import {
  getConnectionTypeOncePicked,
  printMessageForDevelopmentMode,
  disableZoomForDoubleClick, 
  doNotShowContextMenu,
  doNotLetDeleteReferenceNode,
  warnForIncompatibleConnection,
  hideModalIfNodeRemoved,
  warnIfConnectionLimitCrosses,
  
  hideNewPostbackNodeAndModalElementsIfConnectedWithSequenceSingle,
  showNewPostbackNodeAndModalElementsIfConnectedWithSequenceSingle,

  showReferenceModalElementsIfActionButtonHasSpecificValue,
  hideReferenceModalElementsIfActionButtonHasSpecificValue,
  
  displayComponentListAndCreateNodeOnClick,

  toggleOutputConnectionOfNewQuestionOnCreated,
  toggleOutputConnectionOfNewQuestionOnRemoved,
  changeUniqueIdOnCloneNode,
  deleteChildrenNodesWhenDeletingParentNode,
  handleConnectionLimitForRowsWhenConnectionCreated,
  handleConnectionLimitForRowsWhenConnectionRemoved,
} from '../listeners'

const sequence_addon = !! window.xitFlowBuilderData.sequence_addon
const messenger_bot_condition = !! window.xitFlowBuilderData.messenger_bot_condition
const user_input_flow_addon = !! window.xitFlowBuilderData.user_input_flow_addon
const instagram_bot_addon = !! window.xitFlowBuilderData.instagram_addon

const hideFromContextMenu = [
  CONST_COMPONENT.ACTION_BUTTON,
  CONST_COMPONENT.POSTBACK,
  CONST_COMPONENT.COMPONENT_LIST,
  CONST_COMPONENT.BROADCASTING,
  CONST_COMPONENT.SEQUENCE_SINGLE,
  CONST_COMPONENT.USER_INPUT_FLOW_SINGLE,
  CONST_COMPONENT.BUTTON,
  CONST_COMPONENT.QUICK_REPLY,
  CONST_COMPONENT.ECOMMERCE,
]

const hideCloneMenuFromContextMenu = [CONST_COMPONENT.COMPONENT_LIST]

if (! sequence_addon) {
  hideFromContextMenu.push(CONST_COMPONENT.SEQUENCE)
  hideFromContextMenu.push(CONST_COMPONENT.SEQUENCE_SINGLE)
}

if(! user_input_flow_addon) {
  hideFromContextMenu.push(CONST_COMPONENT.USER_INPUT_FLOW)
  hideFromContextMenu.push(CONST_COMPONENT.USER_INPUT_FLOW_SINGLE)
}

if(! messenger_bot_condition) {
  hideFromContextMenu.push(CONST_COMPONENT.CONDITION)
}

const hideFromComponentListBySocketName = [
  CONST_SOCKET.COMPONENT_LIST_INPUT,
  CONST_SOCKET.ECOMMERCE_INPUT,
]

const config = {
  moduleDir: 'modules',
  registryArray: [],
  registryObject: {},
  
  hideFromContextMenu: hideFromContextMenu,
  hideCloneMenuFromContextMenu: hideCloneMenuFromContextMenu,
  hideFromComponentListBySocketName: hideFromComponentListBySocketName,

  undroppableElements: [
    'xitFbGrip', 
    'fbDockmenu', 
    'hoverNodeCard'
  ],

  ignoreNodesWhileCheckingData: [
    CONST_COMPONENT.CONDITION,
  ],

  defaultComponentMappingTypeToClass: {
    'broadcasting': BroadcastComponent,
    'user_input_flow': UserInputFlowComponent,
    'messenger_engagement_plugin': TdotmeComponent,
  },

  eventsAndListeners: [
    { event: 'connectionpick', listener: getConnectionTypeOncePicked },
    { event: 'warn', listener: printMessageForDevelopmentMode },
    { event: 'zoom', listener: disableZoomForDoubleClick },
    { event: 'showcontextmenu', listener: doNotShowContextMenu },
    { event: 'noderemove', listener: doNotLetDeleteReferenceNode },
    { event: 'connectioncreate', listener: warnForIncompatibleConnection },
    { event: 'noderemoved', listener: hideModalIfNodeRemoved },

    { event: 'connectioncreated', listener: warnIfConnectionLimitCrosses },

    { event: 'connectioncreated', listener: hideNewPostbackNodeAndModalElementsIfConnectedWithSequenceSingle },
    { event: 'connectionremoved', listener: showNewPostbackNodeAndModalElementsIfConnectedWithSequenceSingle },

    { event: 'connectioncreated', listener: showReferenceModalElementsIfActionButtonHasSpecificValue },
    { event: 'connectionremoved', listener: hideReferenceModalElementsIfActionButtonHasSpecificValue },

    { event: 'connectioncreated', listener: toggleOutputConnectionOfNewQuestionOnCreated },
    { event: 'connectionremoved', listener: toggleOutputConnectionOfNewQuestionOnRemoved },
    
    { event: 'connectiondrop', listener: displayComponentListAndCreateNodeOnClick },
    
    { event: 'nodeclone', listener: changeUniqueIdOnCloneNode },
    
    { event: 'nodedelete', listener: deleteChildrenNodesWhenDeletingParentNode },
    
    { event: 'connectioncreated', listener: handleConnectionLimitForRowsWhenConnectionCreated },
    { event: 'connectionremoved', listener: handleConnectionLimitForRowsWhenConnectionRemoved },
  ],

  socketToComponent: {
    [CONST_SOCKET.AUDIO_INPUT]: { name: CONST_COMPONENT.AUDIO, lingualName: CONST_LINGUAL_NAME.AUDIO },
    [CONST_SOCKET.BUTTON_INPUT]: { name: CONST_COMPONENT.BUTTON, lingualName: CONST_LINGUAL_NAME.BUTTON },
    // [CONST_SOCKET.CAROUSEL_INPUT]: { name: CONST_COMPONENT.CAROUSEL, lingualName: CONST_LINGUAL_NAME.CAROUSEL },
    // [CONST_SOCKET.CAROUSEL_SINGLE_INPUT]: { name: CONST_COMPONENT.CAROUSEL_ITEM, lingualName: CONST_LINGUAL_NAME.CAROUSEL_ITEM },
    [CONST_SOCKET.CONDITION_INPUT]: { name: CONST_COMPONENT.CONDITION, lingualName: CONST_LINGUAL_NAME.CONDITION },
    [CONST_SOCKET.ECOMMERCE_INPUT]: { name: CONST_COMPONENT.ECOMMERCE, lingualName: CONST_LINGUAL_NAME.ECOMMERCE },
    // [CONST_SOCKET.FACEBOOK_MEDIA_INPUT]: { name: CONST_COMPONENT.FACEBOOK_MEDIA, lingualName: CONST_LINGUAL_NAME.FACEBOOK_MEDIA },
    [CONST_SOCKET.FILE_INPUT]: { name: CONST_COMPONENT.FILE, lingualName: CONST_LINGUAL_NAME.FILE },
    // [CONST_SOCKET.GENERIC_TEMPLATE_INPUT]: { name: CONST_COMPONENT.GENERIC_TEMPLATE, lingualName: CONST_LINGUAL_NAME.GENERIC_TEMPLATE },
    [CONST_SOCKET.IMAGE_INPUT]: { name: CONST_COMPONENT.IMAGE, lingualName: CONST_LINGUAL_NAME.IMAGE },
    
    [CONST_SOCKET.NEW_POSTBACK_INPUT]: { name: CONST_COMPONENT.POSTBACK_NEW, lingualName: CONST_LINGUAL_NAME.POSTBACK_NEW },
    [CONST_SOCKET.NEW_POSTBACK_INPUT_TRIGGER]: { name: CONST_COMPONENT.POSTBACK_NEW, lingualName: CONST_LINGUAL_NAME.POSTBACK_NEW },
    
    // [CONST_SOCKET.OTN_INPUT]: { name: CONST_COMPONENT.OTN, lingualName: CONST_LINGUAL_NAME.OTN },
    // [CONST_SOCKET.OTN_SINGLE_INPUT]: { name: CONST_COMPONENT.OTN_SINGLE, lingualName: CONST_LINGUAL_NAME.OTN_SINGLE },
    
    [CONST_SOCKET.QUICK_REPLY_INPUT]: { name: CONST_COMPONENT.QUICK_REPLY, lingualName: CONST_LINGUAL_NAME.QUICK_REPLY },
    
    [CONST_SOCKET.REFERENCE_INPUT]: { name: CONST_COMPONENT.POSTBACK, lingualName: CONST_LINGUAL_NAME.POSTBACK },
    [CONST_SOCKET.REFERENCE_INPUT_ACTION_BUTTON]: { name: CONST_COMPONENT.POSTBACK, lingualName: CONST_LINGUAL_NAME.POSTBACK },
    
    [CONST_SOCKET.INTERACTIVE_INPUT]: { name: CONST_COMPONENT.INTERACTIVE, lingualName: CONST_LINGUAL_NAME.INTERACTIVE },
  
    [CONST_SOCKET.ROW_INPUT]: { name: CONST_COMPONENT.ROW, lingualName: CONST_LINGUAL_NAME.ROW },
    
    [CONST_SOCKET.SECTION_INPUT]: { name: CONST_COMPONENT.SECTION, lingualName: CONST_LINGUAL_NAME.SECTION },
    
    [CONST_SOCKET.SEQUENCE_INPUT]: { name: CONST_COMPONENT.SEQUENCE, lingualName: CONST_LINGUAL_NAME.SEQUENCE },
    [CONST_SOCKET.SEQUENCE_SINGLE_INPUT]: { name: CONST_COMPONENT.SEQUENCE_SINGLE, lingualName: CONST_LINGUAL_NAME.SEQUENCE_SINGLE },
    
    [CONST_SOCKET.TEXT_INPUT]: { name: CONST_COMPONENT.TEXT, lingualName: CONST_LINGUAL_NAME.TEXT },
    [CONST_SOCKET.TRIGGER_INPUT]: { name: CONST_COMPONENT.TRIGGER, lingualName: CONST_LINGUAL_NAME.TRIGGER },
    
    [CONST_SOCKET.USER_INPUT_FLOW_INPUT]: { name: CONST_COMPONENT.USER_INPUT_FLOW, lingualName: CONST_LINGUAL_NAME.USER_INPUT_FLOW },
    [CONST_SOCKET.USER_INPUT_FLOW_SINGLE_INPUT]: { name: CONST_COMPONENT.USER_INPUT_FLOW_SINGLE, lingualName: CONST_LINGUAL_NAME.USER_INPUT_FLOW_SINGLE },
    
    [CONST_SOCKET.VIDEO_INPUT]: { name: CONST_COMPONENT.VIDEO, lingualName: CONST_LINGUAL_NAME.VIDEO },
    
    [CONST_SOCKET.BROADCASTING_INPUT]: { name: CONST_COMPONENT.BROADCASTING, lingualName: CONST_LINGUAL_NAME.BROADCASTING },
  }
}

if (instagram_bot_addon) {
  config.ignoreNodesWhileCheckingData.push(CONST_COMPONENT.CAROUSEL)
}

export default config