<template>
  <div 
    class="xit-fb-template node"
    @dblclick.stop="handleClick(node)" 
    :class="[camelCase(node.name), hasData(node), isSelected(node)]" 
  >
    <div class="title">
      <p class="title-content"><i class="fas fa-newspaper card-start-icon"></i> {{node.lingualName}}</p>
    </div>

    <div class="card-body-handler card_data_body p-0">
      <div class="card-data">

        <div ref="displayStatHtml"></div>

        <div class="typing_delay clearfix mb-2 p-1 pl-3 pr-3" v-if="node.data.IsTypingOnDisplayChecked">
          <p class="typing_display float-left mb-0 p-0">
            <img :src="typingon_gif"> {{ typing_display }}
          </p>

          <p class="delay_time float-right mb-0" v-if="node.data.delayReplyFor">
            {{ node.data.delayReplyFor }} {{ sec }}
          </p>
        </div>

        <div v-if="node.data.headerText" class="text_message">
          {{ node.data.headerText }}
        </div>

        <div 
          class="mb-1"
          v-if="node.data.headerMediaUrl && (mediaType(node.data.headerMediaUrl) === 'image')" 
        >
          <img class="img-thumbnail" :src="node.data.headerMediaUrl" />
        </div>

        <p 
          class="text-center"
          v-else-if="node.data.headerMediaUrl && (mediaType(node.data.headerMediaUrl) === 'file')" 
        >
          <i class="fas fa-file-alt card-content-icon"></i><br/>
          <span class="text-small">{{ node.data.headerMediaUrl ? '...' + node.data.headerMediaUrl.substring(node.data.headerMediaUrl.length - 10) : null }}</span>
        </p> 

        <audio 
          class="card-data-media-audio" 
          :key="node.data.headerMediaUrl" 
          width="250" 
          height="auto" 
          controls
          v-else-if="(node.data.headerMediaUrl && mediaType(node.data.headerMediaUrl) === 'audio')"
        >
          <source :src="node.data.headerMediaUrl" :type="node.data.mime_type ? node.data.mime_type : ''">
            {{ audio_not_supported }}
        </audio>

        <video 
          :key="node.data.headerMediaUrl" 
          class="card-data-media-video" 
          width="250" 
          height="auto" 
          controls
          v-else-if="node.data.headerMediaUrl && (mediaType(node.data.headerMediaUrl) === 'video')"
        >
          <source :src="node.data.headerMediaUrl" :type="node.data.mime_type ? node.data.mime_type : ''">
          {{ video_not_supported }}
        </video>

        <div v-if="node.data.textMessage" class="text_message">
          {{ node.data.textMessage }}
        </div>

        <div v-if="node.data.footerText" class="text_message">
          {{ node.data.footerText }}
        </div>
       
      </div>

      <div class="card-sockets">
        <div class="output_content">
          <div class="output text-right" v-for="output in outputs()" :key="output.key" :class="hasConnection(output) ? 'has-connection' : ''">
            <div class="output-title">{{output.name}}</div>
            <Socket v-socket:output="output" type="output" :socket="output.socket"></Socket>
          </div>
        </div>
        <div class="input_content">
          <div class="input text-left" v-for="input in inputs()" :key="input.key" :class="hasConnection(input) ? 'has-connection' : ''">
            <Socket v-socket:input="input" type="input" :socket="input.socket"></Socket>
            <div class="input-title">{{input.name}}</div>
          </div>
        </div>
      </div>


    </div>
  </div>
</template>

<script>
import VueRenderPlugin from 'rete-vue-render-plugin'

import { lang } from '../../assets/lang'
import { 
  templateComponentMethods, 
  updateNodeConnections,
  getMediaTypeFromUrlBasedOnExtension,
} from '../../utils/functions'

export default {
  name: 'InteractiveTemplate',
  mixins: [VueRenderPlugin.mixin],
  components: {
    Socket: VueRenderPlugin.Socket
  },

  data: () => ({
    sec: lang.common_info_sec,
    baseUrl: window.xitFlowBuilderData.base_url,
    typing_display: lang.common_info_typing_display,    
    typingon_gif: window.xitFlowBuilderData.typingon_gif,
    audio_not_supported: lang.common_info_audio_not_supported,
    video_not_supported: lang.common_info_video_not_supported, 
  }),

  methods: {
    ...templateComponentMethods,

    mediaType(url) {
      return getMediaTypeFromUrlBasedOnExtension(url)
    },
  },

  async mounted() {
    const result = await this.displayStat(this.$props.node)
    $(this.$refs.displayStatHtml).html(result)

    updateNodeConnections(this.$props.node)
  }
}
</script>

<style scoped>
.card-data-media-audio {border-radius: 7px;}
.card-content-icon { font-size: 50px; color: #dee13d; }
.card-data-media-video {border-radius: 7px;}
.input_content { width:50% !important;margin-top: 52px;}
.output_content{ width:50% !important;float:right !important;}
.card-sockets { padding: 3px 0 !important;}
.xit-fb-template.hasData { min-width: 250px !important; }
</style>