import Rete from 'rete'

import CONST_SOCKET from '../../const/socket'
import lingualName from '../../const/lingualName'
import CONST_COMPONENT from '../../const/component'
import RowTemplate from './RowTemplate'
import { lang } from '../../assets/lang'

export default class RowComponent extends Rete.Component {
  constructor(config) {
    super(CONST_COMPONENT.ROW)

    this.data.component = RowTemplate
    this.socket = config ? config.sockets : null
  }

  builder (node) {
    node.lingualName = lingualName.ROW
    this.setConnection()

    const rowInput = new Rete.Input(CONST_SOCKET.ROW_INPUT, lang.row_socket_input_reply, this.socket.rowInput, false)
    const rowOutput = new Rete.Output(CONST_SOCKET.ROW_OUTPUT, lang.row_socket_output_next, this.socket.rowOutput, true)

    return node
      .addInput(rowInput)
      .addOutput(rowOutput)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {}

  setConnection() {
    // Row may take connections from:
    this.socket.rowInput.combineWith(this.socket.sectionOutputRows)

    // Row may be connected to:
    this.socket.rowOutput.combineWith(this.socket.textInput)
    this.socket.rowOutput.combineWith(this.socket.imageInput)
    this.socket.rowOutput.combineWith(this.socket.videoInput)
    this.socket.rowOutput.combineWith(this.socket.audioInput)
    this.socket.rowOutput.combineWith(this.socket.fileInput)
    this.socket.rowOutput.combineWith(this.socket.ecommerceInput)
    this.socket.rowOutput.combineWith(this.socket.componentListInput)

    if (this.isUserInputFlowAddonAvailable) {
      this.socket.rowOutput.combineWith(this.socket.userInputFlowInput)
    }
  }
}