import ConnectionPathPlugin from 'rete-connection-path-plugin'

import CONST_SOCKET from '../const/socket'
import CONST_COMPONENT from '../const/component'

import AutoArrange from '../plugins/backend/AutoArrange'
import OtherListeners from '../plugins/backend/OtherListeners'
import TogglingConnections from '../plugins/backend/TogglingConnections'
import RemovingConnections from '../plugins/backend/RemovingConnections'

export default [
  // { name: 'nodeHover', plugin: HoverOverNode },
  { 
    name: 'custom-auto-arrange', 
    plugin: AutoArrange,
    options: { margin: { x: 50, y: 10 }, depth: 0 }
  },
  { 
    name: 'connection-path', 
    plugin: ConnectionPathPlugin, 
    options: {
      type: ConnectionPathPlugin.DEFAULT, // DEFAULT or LINEAR transformer
      // transformer: () => ([x1, y1, x2, y2]) => [[x1, y1], [x2, y2]], // optional, custom transformer
      curve: ConnectionPathPlugin.curveBundle, // curve identifier
      // options: { vertical: false, curvature: 0.4 }, // optional
      arrow: { color: '#0D8BF1', marker: 'M5,-7 L5,7 L25,0 z' }
    } 
  },
  { 
    name: 'otherListeners', 
    plugin: OtherListeners 
  },

  { 
    name: 'removingButtonConnections', 
    plugin: RemovingConnections, 
    options: { 
      name: CONST_COMPONENT.BUTTON,
      avoidRemovingWhen: ['new_post_back'],
    },
  },

  { 
    name: 'togglingCarouselConnection', 
    plugin: TogglingConnections, 
    options: { 
      name: CONST_COMPONENT.CAROUSEL, 
      either: CONST_SOCKET.CAROUSEL_OUTPUT, 
      or: CONST_SOCKET.CAROUSEL_OUTPUT_QUICKREPLY 
    },
  },

  { 
    name: 'togglingEcommerceConnection', 
    plugin: TogglingConnections, 
    options: { 
      name: CONST_COMPONENT.ECOMMERCE,
      either: CONST_SOCKET.ECOMMERCE_OUTPUT, 
      or: CONST_SOCKET.ECOMMERCE_OUTPUT_QUICKREPLY 
    },
  },

  { 
    name: 'togglingInteractiveConnection', 
    plugin: TogglingConnections, 
    options: { 
      name: CONST_COMPONENT.INTERACTIVE,
      either: CONST_SOCKET.INTERACTIVE_OUTPUT_BUTTON, 
      or: CONST_SOCKET.INTERACTIVE_OUTPUT_LIST_MESSAGE 
    },
  },
]