import { nanoid } from 'nanoid/async'
import CONST_SOCKET from '../../const/socket'
import { interactiveModal } from './interactiveModal'
import { updateNodeConnections } from '../../utils/functions'

// eslint-disable-next-line no-unused-vars
export default async (node, editor) => {
  const oldData = node.data
  const { value: interactiveData } = await interactiveModal(oldData, node)

  if (interactiveData && interactiveData.textMessage.length > 0) {
    // Create text unique ID if there is not any
    if (! ('uniqueId' in node.data)) {
      node.data.uniqueId = await nanoid(15)
    }

    Object.assign(node.data, interactiveData)

    node.update()

    toggleListMessagesSocketIfMediaHeaderIsSelected(node)

    updateNodeConnections(node)
  }

  return false
}

const toggleListMessagesSocketIfMediaHeaderIsSelected = (node) => {
  const componentElement = $(node.vueContext.$el)

  const interactiveOutputListMessageClass = CONST_SOCKET.INTERACTIVE_OUTPUT_LIST_MESSAGE.toLowerCase()
  const targetElementNode = componentElement.find('.' + interactiveOutputListMessageClass).parent()

  if (
    'headerType' in node.data && 
    'media' === node.data.headerType && 
    'headerMediaUrl' in node.data &&
    node.data.headerMediaUrl
  ) {
    targetElementNode.removeClass('d-block').addClass('d-none')
  } else {
    targetElementNode.removeClass('d-none').addClass('d-block')
  }
}