export const templateComponentMethods = {
  hasData(node) {
    return node.data && (Object.values(node.data).length > 0) 
      ? 'node--has-data'
      : null
  },

  camelCase(name) {
    return camelCase(name)
  },

  startCase(name) {
    return startCase(name)
  },

  isSelected(node) {
    return App.editor.selected.contains(node) 
      ? 'node--selected' 
      : null
  },

  hasConnection(inputOrOutput) {
    return inputOrOutput.connections.length > 0
  },

  handleClick(node) {
    if (CONST_COMPONENT.POSTBACK_NEW === node.name) {
         setHidableNewPostbackModalFieldsStatus(node)
       }
    App.handleNodeData(node, App.editor)
  },

  getSelectedLabelTexts(labelTextsArray = []) {
    return labelTextsArray.join(', ')
  },


  async displayStat (node) {
    return await sleep(250).then(() => {
      const message_sent_stat_addon = window.xitFlowBuilderData.message_sent_stat_addon

      if (! message_sent_stat_addon) {
        return;
      }

      var message_sent_stat = window.xitFlowBuilderData.message_sent_stat

      var uniqueId = ''
      var is_message_sent = false
      // const targetNode = getNextNewPostback(node)

      if(node.name === CONST_COMPONENT.BUTTON)
      {
        uniqueId = node.data.postbackId
      }
      else if(node.name === CONST_COMPONENT.QUICK_REPLY)
      {
        uniqueId = node.data.uniqueId;
        is_message_sent = false
      }
      else
      {
        uniqueId = node.data.uniqueId;
        is_message_sent = true
      }

      var total_sent_click = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].sent_click : 0
      var total_error = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].total_error : 0
      var subscribers = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].subscribers : 0
      var delivered = Object.keys(message_sent_stat).indexOf(uniqueId) > -1 ? message_sent_stat[uniqueId].delivered : 0

      let html = ''

      if (is_message_sent) {
        html += '<ul class="list-group list-group-horizontal list-group-horizontal-custom text-center">'
        html += '<li class="list-group-item"><span><i class="far fa-paper-plane text-info"></i> ' +lang.message_sent_lang+ '</span> <p>' +total_sent_click+ '</p></li>'
        html += '<li class="list-group-item"><span><i class="far fa-check-circle text-success"></i> '+lang.message_sent_stat_delivered+'</span> <p>'+delivered+'</p></li>'
        html += '<li class="list-group-item"><span><i class="far fa-user-circle text-primary"></i> '+lang.message_sent_stat_subscribers+'</span> <p>'+subscribers+'</p></li>'
        html += '<li class="list-group-item"><span><i class="fas fa-bug text-danger"></i> ' +lang.message_sent_stat_error_lang+ '</span> <p>' +total_error+ '</p></li>'
        html += '</ul>'
      }
      else {
        html += '<ul class="list-group list-group-horizontal list-group-horizontal-custom text-center">'
        html += '<li class="list-group-item"><span><i class="far fa-hand-pointer text-info"></i> ' +lang.postback_click_lang+ '</span> <p>' +total_sent_click+ '</p></li>'
        html += '<li class="list-group-item"><span><i class="far fa-user-circle text-primary"></i> '+lang.message_sent_stat_subscribers+'</span> <p>'+subscribers+'</p></li>'
        html += '<li class="list-group-item"><span><i class="fas fa-bug text-danger"></i> ' +lang.message_sent_stat_error_lang+ '</span> <p>' +total_error+ '</p></li>'
        html += '</ul>'
      }

      return html
    })
  }
}

const setHidableNewPostbackModalFieldsStatus = (node) => {
  const hasConnection = hasInputConnectionWith(
    node,
    CONST_SOCKET.NEW_POSTBACK_INPUT,
    CONST_SOCKET.SEQUENCE_SINGLE_OUTPUT
  )

  if (hasConnection) {
    node.newPostbackModalHidableFieldsStatus = true
  } else {
    if (node.newPostbackModalHidableFieldsStatus) {
      delete node.newPostbackModalHidableFieldsStatus
    }
  }
}


export const hasInputConnectionWith = (node, nodeInputSocketName, OtherNodeOutputSocketName) => {
  const input = node.inputs.get(nodeInputSocketName)

  if (input instanceof Rete.Input) {
    const connections = input.connections
    if (Array.isArray(connections)) {
      for (const connection of connections) {
        if (connection.output.key === OtherNodeOutputSocketName) {
          return true
        }
      }
    }
  }

  return false
}

// https://stackoverflow.com/a/10311375
export const alertBeforeClosingBrowser = () => {
  window.onbeforeunload = function (e) {

    if(true === window.hasXitFlowBuilderData) {
      return
    }

    e = e || window.event

    // For IE and Firefox prior to version 4
    if (e) {
      e.returnValue = 'Sure?'
    }

    // For Safari
    return 'Sure?'
  }
}

export const showPerformanceNoticeForFirefox = (afterSeconds) => {
  setTimeout(async () => {      
    if (! document.cookie.split('; ').find(row => row.startsWith('xitFlowBuilderfirfoxPerformanceNotice'))) {
      const browser = getBrowser()
      const message = `You're using ${browser.name}. For better performace, we recommend use Chrome, Opera or Safari.`
      await Swal.fire({
        title: 'Info!',
        text: message,
        icon: 'info',
        confirmButtonText: 'Dont show again',
        showCancelButton: true,
        cancelButtonText: 'Cancel',
      })
      .then((result) => {
        if (result.isConfirmed) {
          document.cookie = "xitFlowBuilderfirfoxPerformanceNotice=true; SameSite=Strict; expires=Fri, 31 Dec 9999 23:59:59 GMT"
        }
      })
    }
  }, afterSeconds)
}

// https://stackoverflow.com/a/16938481
export const getBrowser = () => {
  var ua = navigator.userAgent,tem,M=ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || []; 
  if(/trident/i.test(M[1])) {
    tem = /\brv[ :]+(\d+)/g.exec(ua) || []
    return {
      name:'IE', 
      version:(tem[1] || '')
    }
  }

  if(M[1] === 'Chrome') {
    tem = ua.match(/\bOPR|Edge\/(\d+)/)
    if(tem != null) {
      return {
        name:'Opera', 
        version: tem[1]
      }
    }
  }

  M = M[2]? [M[1], M[2]]: [navigator.appName, navigator.appVersion, '-?']
  if((tem=ua.match(/version\/(\d+)/i)) != null) {
    M.splice(1, 1, tem[1])
  }

  return {
    name: M[0],
    version: M[1]
  }
}

