export function hasDuplicates(array) {
  return (new Set(array)).size !== array.length;
}

export const createSelect2DynamicOption = (element) => {
  // var base_url = window.xitFlowBuilderData.base_url
  // var instagram_bot_addon = window.xitFlowBuilderData.instagram_addon
  // const social_media = instagram_bot_addon ? 'ig' : 'fb'

  $(element).select2({
    tags: true,
    width: '100%',
    dropdownParent: $('#swal2-content'),
  }).on('select2:select', function(e) {
    const data = e.params.data
    const tagsList = $(this).select2('data')

    if (tagsList.find(val => (val.selected === true && val.text === data.text)) !== undefined) {
      tagsList.push({ id: data.id, text: data.text })
      $(element).select2('data', tagsList)
    } else { 
      $.ajax({
        type:'POST',
        dataType:'JSON',
        url: window.xitFlowBuilderData.routes.common_create_label_and_assign,
        data:{ whatsapp_bot_id, label_name: data.text },
        beforeSend: function (xhr) {
          xhr.setRequestHeader('X-CSRF-TOKEN', csrf_token);
        },
        success: function(response) {
          if(response.error) {
            Swal.showValidationMessage('Could not create label.')
          } else {
            const isDummyOptionAdded = $(element).find("option[value='" + data.text + "']")
            if (isDummyOptionAdded) {
              isDummyOptionAdded.remove();
            }
            
            const newOption = new Option(response.text, response.id, true, true)
            $(element).append(newOption).trigger('change');
          }
        }
      })
    } 
  })
}

export const makeAjaxCall = (callback, data) => {
  const parameters = {
    url: data.url,
    dataType: 'JSON',
    beforeSend: function (xhr) {
      xhr.setRequestHeader('X-CSRF-TOKEN', csrf_token);
    },
    success: callback,
  }

  if (data.method && ('get' === data.method.toLowerCase())) {
    parameters.type = 'GET'
  } else {
    parameters.type = 'POST'
    parameters.data = data.data
  }

  $.ajax(parameters)
}

