import { lang } from '../../assets/lang'
// import { delayHtml } from '../../data/modal/delayHtml'

export const interactiveHtml =
'<div class="header-wrapper">' +
  '<div class="form-group text-left mt-2">' +
    '<label class="form-label pb-2">' + lang.interactive_html_label_header + '</label>' +
    '<div class="selectgroup selectgroup-pills">' +
      '<label class="selectgroup-item">' +
        '<input type="radio" name="header-type" value="text" class="selectgroup-input" checked="">' +
        '<span class="selectgroup-button selectgroup-button-icon">' +
          '<i class="fas fa-keyboard"></i>' +
        '</span>' +
      '</label>' +
      '<label class="selectgroup-item">' +
        '<input type="radio" name="header-type" value="media" class="selectgroup-input">' +
        '<span class="selectgroup-button selectgroup-button-icon">' +
          '<i class="fas fa-upload"></i>' +
        '</span>' +
      '</label>' +
    '</div>' +
  '</div>' +

  '<div class="form-group text-left mt-2">' +
    '<div class="header-text">' +
      '<label for="header-text-text">' + lang.interactive_html_label_text + '</label>' +
      '<input type="text" class="form-control" id="header-text-text" placeholder="' + lang.interactive_html_ph_header_text + '">' +
    '</div>' +

    '<div class="header-media d-none">' +
      '<div class="header-media-url pb-1">' + 
        '<label for="header-media-url-input">' + lang.interactive_html_label_media_url + '</label>' +
        '<input type="text" class="form-control" id="header-media-url-input" placeholder="' + lang.interactive_html_ph_media_url + '">' +
      '</div>' +

      '<div class="header-media-dropzone">' +
        '<label for="">' + lang.interactive_html_label_media_upload + '</label>' +
        '<div id="file-upload-dropzone" class="dropzone d-flex justify-content-center align-items-center mb-1">' +
          '<div class="dz-default dz-message">' +
            '<input type="file" class="d-none">' +
            '<i class="fas fa-cloud-upload-alt"></i>' +
          '</div>' +
        '</div>' +
        '<span class="text-small text-muted"><p>Image:5MB, Audio/Video:16MB, File:100MB</p></span>' +
      '</div>' +
    '</div>' +    
  '</div>' +
'</div>' +

'<div class="body-wrapper mt-4">' +             
  '<div class="form-group text-left mt-2">' +
    '<label>' + lang.interactive_html_label_body + '</label><br/>' +
    '<div class="body-text-custom-variable d-inline-block lead-user-name">' + 
      '<a title="' + lang.interactive_html_info_custom_variable + '" data-toggle="dropdown" data-toggle-second="tooltip" data-placement="top" role="button" aria-haspopup="true" aria-expanded="false" class="btn btn-link btn-sm p-0 p-1 dropdown-toggle"><i class="fa fa-cogs"></i> ' + lang.interactive_html_title_custom + '</a>' +
      '<div class="dropdown-menu" data-animation="fadeIn"></div>' +
    '</div>' + 
    
    '<span class="d-inline-block lead-user-name">' + 
      '<a id="text-text-spintext-firstname" title="' + lang.interactive_html_info_name + '" data-toggle="tooltip" data-placement="top" class="btn-sm lead_first_name button-outline"><i class="fa fa-user"></i> ' + lang.interactive_html_title_name + '</a>' +
    '</span>' +

    '<div class="clearfix"></div><textarea class="form-control text_message"  name="body-text" id="body-text"></textarea>' +
  '</div>' +
'</div>' +

'<div class="footer-wrapper mt-4">' +             
  '<div class="form-group text-left mt-2">' +
    '<label for="footer-text-text">' + lang.interactive_html_label_footer + '</label>' +
    '<input type="text" class="form-control" id="footer-text-text" placeholder="' + lang.interactive_html_ph_footer_text + '">' +
  '</div>' +
'</div>'
