import { nanoid } from 'nanoid/async'
import { rowModal } from './rowModal'
import { updateNodeConnections } from '../../utils/functions'

// eslint-disable-next-line
export default async (node, editor) => {
  const oldData = node.data
  const { value: rowData } = await rowModal(oldData, node)

  if (rowData && Object.values(rowData).length > 0) {
    // Create text unique ID if there is not any
    if (! ('postbackId' in node.data)) {
      node.data.postbackId = await nanoid(15)
    }

    Object.assign(node.data, rowData)

    node.update()
    updateNodeConnections(node)
  }

  return false
}