import Swal from 'sweetalert2'
import Dropzone from 'dropzone'
import { interactiveHtml } from './interactiveHtml'
import {
  prepareDropdownLists,
  handleOldDataOfDelay,
  sweetAlertCommonParams,
} from '../../utils/functions'
import { lang } from '../../assets/lang'

export const interactiveModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: lang.text_modal_title,
    html: interactiveHtml,

    willOpen: () => {
      const headerType = oldData.headerType || 'text'
      const headerMediaUrlInputElement = $('#header-media-url-input')

      $('input[name="header-type"][value="' + headerType + '"]').prop('checked', true)
      $('#header-text-text').val(oldData.headerText || '')
      headerMediaUrlInputElement.val(oldData.headerMediaUrl || '')
      $('#footer-text-text').val(oldData.footerText || '')

      toggleHeaderType(headerType)
      $('input[name="header-type"]').on('change', (event) => {
        toggleHeaderType(event.target.value)            
      })

      let serverGeneratedFilename = ''
      Dropzone.autoDiscover = false
      window.flowBuilderUploadedFileData = {}

      // eslint-disable-next-line
      const uploadMediaDropzone = new Dropzone("#file-upload-dropzone", {
        url: window.xitFlowBuilderData.routes.flow_builder_upload_media,
        maxFilesize: window.xitFlowBuilderData.upload_limit.file,
        uploadMultiple: false,
        paramName: "media_file",
        createImageThumbnails: true,
        acceptedFiles: ".png, .jpg, .jpeg, .webp, .JPEG, .JPG, .PNG, .WEBP, .aac, .amr, .mp3, .AAC, .AMR, .MP3, .mp4, .MP4, .3gpp, .3GPP, .doc, .docx, .pdf, .txt, .ppt, .pptx, .xls, .xlsx, .DOC, .DOCX, .PDF, .TXT, .PPT, .PPTX, .XLS, .XLSX",
        maxFiles: 1,
        addRemoveLinks: true,
        headers:{
          'X-CSRF-TOKEN': window.xitFlowBuilderData.csrf_token
        },
        
        // eslint-disable-next-line
        success: async function(file, response) {
          // Display message if error
          if (false === response.status) {
            await Swal.fire({
              icon: 'error',
              text: response.message,
              title: 'Error!',
            });

            return
          }

          if (response.status) {
            serverGeneratedFilename = response.file
            window.flowBuilderUploadedFileData = {
              mime_type: response.file_type,
              file: response.file,
            }

            headerMediaUrlInputElement.val(response.file)
          }
        },

        // eslint-disable-next-line
        removedfile: function(file) {
          const fileData = window.flowBuilderUploadedFileData
          if (fileData.file && (fileData.file === serverGeneratedFilename)) {
            // eslint-disable-next-line
            const result = deleteUploadedFile(serverGeneratedFilename)
              .then(response => {
                if (response.status) {
                  window.flowBuilderUploadedFileData = {}
                  headerMediaUrlInputElement.val('')
                  $('#file-upload-dropzone .dz-preview').remove()
                  Dropzone.forElement('#file-upload-dropzone').removeAllFiles(true)
                }
              }).catch(error => {
                if (error.status !== '200' && error.statusText) {
                  const message = error.status + ' ' + error.statusText
                  alert(message)
                } else {
                  console.log(error)
                }
              })
          }
        },
      })

      // Removes previous files if there is any
      Dropzone.forElement('#file-upload-dropzone').removeAllFiles(true)

      getCustomFieldVariableViaAjax(function(response) {
        const optionsArray = response.custom_field_variable_dropdown_array || []
        const html = prepareDropdownLists(optionsArray)
        $('.body-text-custom-variable .dropdown-menu').html(html)  
      })
 
      $('.lead_first_name').on('click', function() {
        var textAreaTxt = $('.emojionearea-editor').html();

        var lastIndex = textAreaTxt.lastIndexOf("<br>");   
        var lastTag = textAreaTxt.substr(textAreaTxt.length - 4); 
        lastTag=lastTag.trim(lastTag);

        if(lastTag=="<br>")
        textAreaTxt = textAreaTxt.substring(0, lastIndex); 

        var txtToAdd = " #LEAD_USER_FIRST_NAME# ";
        var new_text = textAreaTxt + txtToAdd;
        $('.emojionearea-editor').html(new_text);
        $('.emojionearea-editor').trigger('blur');
      });

      $('.dropdown-menu').on('click', '.dropdown-item', function(event) {
        const selectedText = event.target.innerText

        var textAreaTxt = $('.emojionearea-editor').html()
        
        var lastIndex = textAreaTxt.lastIndexOf("<br>");   
        var lastTag = textAreaTxt.substr(textAreaTxt.length - 4);
        lastTag=lastTag.trim(lastTag)

        if(lastTag=="<br>")
          textAreaTxt = textAreaTxt.substring(0, lastIndex)
        
        var txtToAdd = " #" + selectedText + "# "
        var new_text = textAreaTxt + txtToAdd;
        $('.emojionearea-editor').html(new_text);
        $('.emojionearea-editor').trigger('blur');
      });

      $('#body-text').html(oldData.textMessage || '')

      handleOldDataOfDelay(oldData)

      $("#body-text").emojioneArea({
        autocomplete: false,
        pickerPosition: "bottom"
      })

      $('[data-toggle="tooltip"]').tooltip()  
      $('[data-toggle-second="tooltip"]').tooltip()  
    },

    preConfirm: () => {
      const headerType = $('input[name="header-type"]:checked').val()
      const headerText = $('#header-text-text').val()

      const headerMediaUrl = $('#header-media-url-input').val()
      const mediaType = window.flowBuilderUploadedFileData.mime_type || ''
      
      const textMessage = $('#body-text').val()
      const footerText = $('#footer-text-text').val()

      if (headerText && headerText.length > 60) {
        return Swal.showValidationMessage(lang.interactive_modal_msg_header_text)
      }

      if (! textMessage) {
        return Swal.showValidationMessage(lang.interactive_modal_msg_body_message_required)
      }

      if (textMessage.length > 1024) {
        return Swal.showValidationMessage(lang.interactive_modal_msg_body_message_length)
      }

      if (footerText && footerText.length > 60) {
        return Swal.showValidationMessage(lang.interactive_modal_msg_footer_text)
      }

      return {
        headerType,
        headerText,
        mediaType,
        headerMediaUrl, 
        textMessage,
        footerText,
      }
    },

    didDestroy: () => {
      const tooltip = $('[data-toggle="tooltip"]')
      if (tooltip) {
        $('[data-toggle="tooltip"]').tooltip('dispose')
      }
    },
  })
}

const getCustomFieldVariableViaAjax = (callback) => {
  // const base_url = window.xitFlowBuilderData.base_url
  // const instagram_bot_addon = window.xitFlowBuilderData.instagram_addon
  // const media_type = instagram_bot_addon ? 'ig' : 'fb'

  $.ajax({
    method: 'POST',
    dataType: 'JSON',
    url: window.xitFlowBuilderData.routes.flow_builder_get_custom_field_variable_dropdown,
    beforeSend: function (xhr) {
        xhr.setRequestHeader('X-CSRF-TOKEN', window.xitFlowBuilderData.csrf_token);
    },
    success: callback
  })
}

const toggleHeaderType = (selected) => {
  const text = $('.header-text')
  const media = $('.header-media')

  if (selected === 'text') {
    media.removeClass('d-block').addClass('d-none')
    text.removeClass('d-none').addClass('d-block')
  } else {
    text.removeClass('d-block').addClass('d-none')
    media.removeClass('d-none').addClass('d-block')
  }
}

const deleteUploadedFile = async (file) => {
  return await $.ajax({
    type: 'POST',
    url: window.xitFlowBuilderData.routes.flow_builder_delete_media,
    dataType: 'JSON',
    beforeSend: function (xhr) {
      xhr.setRequestHeader('X-CSRF-TOKEN', window.xitFlowBuilderData.csrf_token);
    },
    data: { file }, 
  })
}