import Swal from 'sweetalert2'
import { rowHtml } from './rowHtml'
import { sweetAlertCommonParams } from '../../utils/functions'
import { lang } from '../../assets/lang'

export const rowModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: lang.row_modal_title,
    html: rowHtml,

    willOpen: () => {
      $('#row-row-title').val(oldData.title || '')
      $('#row-row-description').html(oldData.description || '')

      $("#row-row-description").emojioneArea({
        autocomplete: false,
        pickerPosition: "bottom"
      })
    },

    preConfirm: () => {
      const title = $('#row-row-title').val()
      const description = $('#row-row-description').val()

      if (! title) {
        Swal.showValidationMessage(lang.row_modal_msg_title_required)
      }

      if (title.length > 24) {
        Swal.showValidationMessage(lang.row_modal_msg_title_length)
      }

      if (description && description.length > 72) {
        Swal.showValidationMessage(lang.row_modal_msg_description_length)
      }
  
      return {
        title,
        description,
      }
    },    
  })
}