import { App } from '../../App'
import CONST_SOCKET from '../../const/socket'
import { sequenceSingleModal } from './sequenceSingleModal'
import { toggleNodeSockets, updateNodeConnections } from '../../utils/functions'

// eslint-disable-next-line
export default async (node, editor) => {
  const oldData = node.data
  const { value: sequenceSingleData } = await sequenceSingleModal(oldData, node)

  if (sequenceSingleData && Object.values(sequenceSingleData).length >= 0) {
    Object.assign(node.data, sequenceSingleData)
    toggleSequenceSingleSocketsAndRemoveConnections(node)
    node.update()
    updateNodeConnections(node)
  }

  return false
}

const toggleSequenceSingleSocketsAndRemoveConnections = (node) => {
  const sequenceOutputSequenceClass = CONST_SOCKET.SEQUENCE_SINGLE_OUTPUT.toLowerCase()
  if (node.data.sequenceSingleType && '2' === node.data.sequenceSingleType) {
    const connections = node.outputs.get(CONST_SOCKET.SEQUENCE_SINGLE_OUTPUT).connections.slice()
    if (connections.length > 0) {
      connections.forEach(singleConnection => {
        App.editor.removeConnection(singleConnection)
      })
    }
    toggleNodeSockets(node, sequenceOutputSequenceClass, 'hide')
  } else {
    toggleNodeSockets(node, sequenceOutputSequenceClass, 'show')
  }
}