import Swal from 'sweetalert2'
import { sectionHtml } from './sectionHtml'
import { sweetAlertCommonParams } from '../../utils/functions'
import { lang } from '../../assets/lang'

export const sectionModal = async (oldData, node) => {
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: lang.section_modal_title,
    html: sectionHtml,

    willOpen: () => {
      $('#section-section-title').val(oldData.title || '')
    },

    preConfirm: () => {
      const title = $('#section-section-title').val()

      if (! title) {
        return Swal.showValidationMessage(lang.section_modal_msg_title_required)
      }

      if (title.length > 24) {
        return Swal.showValidationMessage(lang.section_modal_msg_title_length)
      }
  
      return {
        title
      }
    },    
  })
}