import Rete from 'rete'

import InteractiveTemplate from './InteractiveTemplate'
import CONST_SOCKET from '../../const/socket'
import lingualName from '../../const/lingualName'
import CONST_COMPONENT from '../../const/component'
import { lang } from '../../assets/lang'

export default class InteractiveComponent extends Rete.Component {
  constructor(config) {
    super(CONST_COMPONENT.INTERACTIVE)

    this.data.component = InteractiveTemplate
    this.socket = config ? config.sockets : null
  }

  builder(node) {
    node.lingualName = lingualName.INTERACTIVE
    this.setConnection()

    const interactiveInput = new Rete.Input(CONST_SOCKET.INTERACTIVE_INPUT, lang.interactive_socket_input_reply, this.socket.interactiveInput, false)
    const interactiveOutput = new Rete.Output(CONST_SOCKET.INTERACTIVE_OUTPUT, lang.interactive_socket_output_next, this.socket.interactiveOutput, false)
    const interactiveOutputButton = new Rete.Output(CONST_SOCKET.INTERACTIVE_OUTPUT_BUTTON, lang.interactive_socket_output_buttons, this.socket.interactiveOutputButton, true)
    const interactiveOutputListMessage = new Rete.Output(CONST_SOCKET.INTERACTIVE_OUTPUT_LIST_MESSAGE, lang.interactive_socket_output_list_messages, this.socket.interactiveOutputListMessage, false)

    return node
      .addInput(interactiveInput)
      .addOutput(interactiveOutput)
      .addOutput(interactiveOutputButton)
      .addOutput(interactiveOutputListMessage)
  }

  // eslint-disable-next-line
  worker(node, inputs, outputs) {}
  
  setConnection() {
    // Interactive may be connected to:
    this.socket.interactiveOutput.combineWith(this.socket.imageInput)
    this.socket.interactiveOutput.combineWith(this.socket.videoInput)
    this.socket.interactiveOutput.combineWith(this.socket.audioInput)
    this.socket.interactiveOutput.combineWith(this.socket.fileInput)
    this.socket.interactiveOutput.combineWith(this.socket.ecommerceInput)
    this.socket.interactiveOutput.combineWith(this.socket.componentListInput)
    this.socket.interactiveOutput.combineWith(this.socket.interactiveInput)

    this.socket.interactiveOutputButton.combineWith(this.socket.buttonInput)
    this.socket.interactiveOutputListMessage.combineWith(this.socket.quickReplyInput)
  }
}