import cleanXss from 'xss'
import Swal from 'sweetalert2'
import { nanoid } from 'nanoid/async'

import { lang } from '../../assets/lang'
import { sequenceSingleHtml } from './sequenceSingleHtml'
import { 
  makeAjaxCall,
  uploadFileViaDropzone,
  sweetAlertCommonParams,
  removeSpinner,
  callFunctionWithInterval,
  appendSpinner,
  mimeTypesMap,
  isPlainObject,
} from '../../utils/functions'

let scriptGlobalOldData
let sequenceSingleDropzone
let botTemplateResponseArray = []
let postbackListResponseData = []
const ajaxCallStates = {
  hasBotTemplateData: false,
  hasPostbackData: false,
}
const whatsapp_bot_id = window.xitFlowBuilderData.whatsapp_bot_id

// eslint-disable-next-line
export const sequenceSingleModal = async (oldData, node) => {
  scriptGlobalOldData = oldData
  const commonParams = sweetAlertCommonParams(node)
  
  return await Swal.fire({
    ...commonParams,
    title: lang.sequence_single_modal_title,
    html: sequenceSingleHtml,

    willOpen: () => {
      $('.select2').select2({ 
        width: '100%',
        dropdownParent: $('#swal2-content')
      })

      const sequenceSingleTypeElement = $('#squence-single-type-wrapper')
      const botTemplateElement = $('#bot-template-input')
      const sequenceSingleHoursElement = $('#sequence-single-hours-group')
      const sequenceSingleDaysElement = $('#sequence-single-days-group')
      const botTemplateHeaderMediaUrlInputElement = $('#bot-template-header-media-url-input')

      // Displays spinner
      appendSpinner(sequenceSingleTypeElement, true)

      // Handles loading bot templates
      const botTemplateAjaxData = {
        method: 'GET',
        url: window.xitFlowBuilderData.routes.get_bot_templates,
      }

      makeAjaxCall(function(response) {
        ajaxCallStates.hasBotTemplateData = true
        botTemplateResponseArray = response
        const botTemplateOptions = prepareSelectOptions(response)
        botTemplateElement.html(botTemplateOptions)
        botTemplateElement.val(oldData.botTemplateId || '').trigger('change')
      }, botTemplateAjaxData)

      // Handles loading postback list
      const postbackListAjaxData = {
        url: window.xitFlowBuilderData.routes.flow_builder_get_dropdown_postback,
        data: {
          whatsapp_bot_id
        }
      }

      makeAjaxCall(function(response) {
        ajaxCallStates.hasPostbackData = true
        postbackListResponseData = response.dropdown_str
      }, postbackListAjaxData)

      callFunctionWithInterval(() => {
        if (ajaxCallStates.hasBotTemplateData && ajaxCallStates.hasPostbackData) {
          removeSpinner(sequenceSingleTypeElement)
          bootstrap()
          return true
        }
        return false
      }, 50)

      function bootstrap() {
        let sequenceSingleType = oldData.sequenceSingleType || '1'
        $('input[name="sequence-single-radio"][value="' + sequenceSingleType + '"]').prop('checked', true)
        $('.button-onDemand-field').hide()

        if ('1' === oldData.sequenceSingleType) {
          $('#sequence-single-hours').val(oldData.promotional || '').trigger('change')
          sequenceSingleHoursElement.show()
        } else {
          $('#sequence-single-days').val(oldData.nonPromotional || '').trigger('change')
          sequenceSingleDaysElement.show()
        }
        
        toggleSequenceSingleTypeAndUploadBox(sequenceSingleType)
        $('input[name="sequence-single-radio"]').on('change', (event) => {
          const selectedType = event.target.value
          sequenceSingleType = selectedType
          toggleSequenceSingleTypeAndUploadBox(selectedType)
        })

        $(botTemplateElement).on('change', function() {
          handleUploadAndButtonFieldsOnTemplateSelection(
            sequenceSingleType,
            $(this).val(),
            $(this).find(':selected').data('header-type'),
            $(this).find(':selected').data('header-subtype'),
            $(this).find(':selected').data('button-type')
          )
        })
      }

      // Handles uploading files
      sequenceSingleDropzone = uploadFileViaDropzone({ 
        elementForInsertingUrlOntoInput: botTemplateHeaderMediaUrlInputElement 
      })
    },

    preConfirm: () => {
      const isPromotionalChecked = $('#sequence-single-promotional').prop('checked')
      const isNonPromotionalChecked = $('#sequence-single-non-promotional').prop('checked')

      let promotional = $('#sequence-single-hours').val()
      let promotionalText = $('#sequence-single-hours option:selected').text()

      let nonPromotional = $('#sequence-single-days').val()
      let nonPromotionalText = $('#sequence-single-days option:selected').text()

      const sequenceSingleType = $('input[name="sequence-single-radio"]:checked').val()
      const botTemplateId = $('#bot-template-input').val() || null
      const botTemplateText = $('#bot-template-input option:selected').text() || null
      const botTemplateHeaderType = $('#bot-template-input option:selected').data('header-type') || null
      const botTemplateButtonType = $('#bot-template-input option:selected').data('button-type') || null
      const botTemplateHeaderMediaUrl = $('#bot-template-header-media-url-input').val() || null

      const botTemplateQuickreplyButtonsElement = $('.bot-template-quickreply-buttons')
      const botTemplateQuickreplyButtonInputLabels = []
      const botTemplateQuickreplyButtonValues = botTemplateQuickreplyButtonsElement.map((index, element) => { // eslint-disable-line
        if (new Boolean(element.value).valueOf() === false) {
          botTemplateQuickreplyButtonInputLabels.push(cleanXss($(element).prev().text()))
        }
        return element.value
      }).get()

      const botTemplateDynamicVariableElement = $('.bot-template-dynamic-variable')
      const botTemplateDynamicVariableValues = botTemplateDynamicVariableElement.map((index, element) => { // eslint-disable-line
        const label = cleanXss($(element).prev().text())
        const position = $(element).data('position')
        return { label, value: element.value, position }
      }).get()

      if (! isPromotionalChecked && ! isNonPromotionalChecked) {
        Swal.showValidationMessage(lang.sequence_single_modal_msg_promotional_or_non_promotional)
      }

      if ('2' === sequenceSingleType) {
        if (! botTemplateId) {
          return Swal.showValidationMessage(lang.sequence_single_modal_msg_bot_template)
        }

        if ('quick_reply' === botTemplateButtonType) {
          if (botTemplateQuickreplyButtonsElement.length !== botTemplateQuickreplyButtonValues.filter(Boolean).length) {
            const message = lang.sequence_single_modal_msg_quickreply_buttons_required + ' ' + botTemplateQuickreplyButtonInputLabels.join(',')
            return Swal.showValidationMessage(message)
          }
        }

        const emptyDynamicVariableFields = botTemplateDynamicVariableValues
        .filter(item => new Boolean(item.value).valueOf() === false)

        if ((botTemplateDynamicVariableElement.length ===
          botTemplateDynamicVariableValues.length) &&
          emptyDynamicVariableFields.length > 0
        ) {
          const message = lang.broadcasting_msg_quickreply_buttons_required + ' ' + emptyDynamicVariableFields.map(item => item.label).join(',')
          return Swal.showValidationMessage(message)
        }

        if ('media' === botTemplateHeaderType) {
          if (! botTemplateHeaderMediaUrl) {
            return Swal.showValidationMessage(lang.sequence_single_modal_msg_upload_or_inser_file_url)
          }
        }
      }

      if (isPromotionalChecked && ! promotional) {
        Swal.showValidationMessage(lang.sequence_single_modal_msg_promotional)
      }

      if (isNonPromotionalChecked && ! nonPromotional) {
        Swal.showValidationMessage(lang.sequence_single_modal_msg_non_promotional)
      }

      if (isPromotionalChecked && promotional) {
        nonPromotional = ''
        nonPromotionalText = ''
      } else if (isNonPromotionalChecked && nonPromotional) {
        promotional = ''
        promotionalText = ''
      }
  
      return {
        isPromotionalChecked,
        isNonPromotionalChecked,

        promotional,
        promotionalText,

        nonPromotional,
        nonPromotionalText,

        sequenceSingleType,
        botTemplateId,
        botTemplateText,
        botTemplateHeaderType,
        botTemplateButtonType,
        botTemplateHeaderMediaUrl,
        botTemplateQuickreplyButtonValues,
        botTemplateDynamicVariableValues,
      }
    },    
  })
}

const toggleSequenceSingleTypeAndUploadBox = (sequenceSingleType) => {
  const botTemplateWrapperElement = $('#bot-template-wrapper')
  const sequenceSingleHoursElement = $('#sequence-single-hours-group')
  const sequenceSingleDaysElement = $('#sequence-single-days-group')

  if ('1' === sequenceSingleType) {
    sequenceSingleDaysElement.hide()
    sequenceSingleHoursElement.show()
    botTemplateWrapperElement.removeClass('d-block').addClass('d-none')
    handleUploadAndButtonFieldsOnTemplateSelection(
      sequenceSingleType, null, null, null, null
    )
  } else {
    sequenceSingleHoursElement.hide()
    sequenceSingleDaysElement.show()
    botTemplateWrapperElement.removeClass('d-none').addClass('d-block')    
    handleUploadAndButtonFieldsOnTemplateSelection(
      sequenceSingleType,
      $('#bot-template-input').val(),
      $('#bot-template-input').find(':selected').data('header-type'),
      $('#bot-template-input').find(':selected').data('header-subtype'),
      $('#bot-template-input').find(':selected').data('button-type'),
    )
  }  
}

const handleUploadAndButtonFieldsOnTemplateSelection = (
  sequenceSingleType, 
  templateId, 
  headerType,
  headerSubtype, 
  buttonType
) => {
  const botTemplateQuickreplyButtonElement = $('#bot-template-quickreply-button-wrapper')
  const botTemplateDynamicVariableElement = $('#bot-template-dynamic-variable-wrapper')
  const botTemplateHeaderMediaElement = $('#bot-template-header-media-wrapper')
  const botTemplateHeaderMediaUrlElement = $('#bot-template-header-media-url-input')

  if ('2' === sequenceSingleType && 'media' === headerType) {
    if (mimeTypesMap[headerSubtype]) {
      const acceptedFiles = mimeTypesMap[headerSubtype].join(',')
      $(sequenceSingleDropzone.element).find('[type="file"]').attr('accept', acceptedFiles)
      sequenceSingleDropzone.options.acceptedFiles = acceptedFiles
    } else {
      $(sequenceSingleDropzone.element).find('[type="file"]').attr('accept', 'unknown')
      sequenceSingleDropzone.options.acceptedFiles = 'unknown'
    }

    botTemplateHeaderMediaUrlElement.val(scriptGlobalOldData.botTemplateHeaderMediaUrl || '')
    botTemplateHeaderMediaElement.removeClass('d-none').addClass('d-block')
  } else {
    botTemplateHeaderMediaElement.removeClass('d-block').addClass('d-none')
    $(sequenceSingleDropzone.element).find('[type="file"]').attr('accept', 'unknown')
    sequenceSingleDropzone.options.acceptedFiles = 'unknown'
  }
  
  if ('2' === sequenceSingleType && 'quick_reply' === buttonType) {
    manageButtonFieldsWithPostbackList(getButtonList(templateId))
    botTemplateQuickreplyButtonElement.removeClass('d-none').addClass('d-block')
  } else {
    botTemplateQuickreplyButtonElement.removeClass('d-block').addClass('d-none')
  }

  if ('2' === sequenceSingleType) {
    manageDynamicVariableFieldsWithValues(getDynamicVariableList(templateId))
    botTemplateDynamicVariableElement.removeClass('d-none').addClass('d-block')
  } else {
    botTemplateDynamicVariableElement.removeClass('d-block').addClass('d-none')
  }
}

const getButtonList = (templateId) => {
  const template = botTemplateResponseArray.find(item => parseInt(item.id, 10) === parseInt(templateId, 10))
  if(template && template.button_content) {
    const content = JSON.parse(template.button_content || '{}')
    if (content && 'buttons' === content.type && Array.isArray(content.buttons)) {
      return content.buttons
    }
  }

  return []
}

const manageButtonFieldsWithPostbackList = async (buttons) => {
  const botTemplateQuickreplyButtonElement = $('#bot-template-quickreply-button-wrapper')
  botTemplateQuickreplyButtonElement.html('')
  let index = 0

  for (const button of buttons) {
    const uniqueId = await nanoid(15)
    const sanitizedButtonText = cleanXss(button.text)

    const html = '' + 
      '<div class="form-group pb-3">' +
        '<label class="pb-1 control-label">' + sanitizedButtonText + '</label>' +
        '<select id="bot-template-quickreply-buttons-' + uniqueId + '" class="form-control select2 bot-template-quickreply-buttons">' + postbackListResponseData + '</select>' +
        '<script type="text/javascript">$("#bot-template-quickreply-buttons-' + uniqueId + '").select2({width: "100%",dropdownParent: $("#swal2-content")})</script>' +
      '</div>'

    botTemplateQuickreplyButtonElement.append(html).promise().done(() => {
      let previousValue = ''
      if (scriptGlobalOldData.botTemplateQuickreplyButtonValues && 
        scriptGlobalOldData.botTemplateQuickreplyButtonValues[index]
      ) {
        previousValue = scriptGlobalOldData.botTemplateQuickreplyButtonValues[index]
      }
      $('#bot-template-quickreply-buttons-' + uniqueId).val(previousValue).trigger('change')
    })

    index++
  }
}

const getDynamicVariableList = (templateId) => {
  const content = botTemplateResponseArray.find(item => parseInt(item.id, 10) === parseInt(templateId, 10))
  if(content && content.variable_map) {
    const parsedContent = JSON.parse(content.variable_map || '{}')
    if (parsedContent && parsedContent.body && isPlainObject(parsedContent.body)) {
      return Object.values(parsedContent.body)
    }
  }

  return []
}

const manageDynamicVariableFieldsWithValues = async (variables) => {
  const botTemplateDynamicVariableElement = $('#bot-template-dynamic-variable-wrapper')
  botTemplateDynamicVariableElement.html('')
  let position = 0

  for (const variable of variables) {
    position++
    
    if (! variable.startsWith('#!') && ! variable.endsWith('!#')) {
      continue
    } 

    const sanitizedVariableText = cleanXss(variable)
    const trimmedVariableText = sanitizedVariableText.replace(/[#|!]/gm, '')

    let previousValue = ''
    if (scriptGlobalOldData.botTemplateDynamicVariableValues) {
      const found = scriptGlobalOldData.
        botTemplateDynamicVariableValues.find(item => item.label === variable)
      previousValue = found ? found.value : ''
    }

    const html = '' + 
      '<div class="form-group pb-3">' +
        '<label class="pb-1 control-label">' + trimmedVariableText + '</label>' +
        '<input type="text" class="form-control bot-template-dynamic-variable" data-position="' + position + '" value="' + previousValue + '">' +
      '</div>'

    botTemplateDynamicVariableElement.append(html)
  }
}

const prepareSelectOptions = (optionsArray, defaultValue = '') => {
  let html = ''

  for (const option of optionsArray) {
    if ('object' === typeof option) {
      const dataTypes = '' +
        'data-header-type="' + option.header_type + '"' +
        'data-header-subtype="' + option.header_subtype + '"' +
        'data-button-type="' + option.button_type + '"'

      const label = option.template_name + ' (' + option.header_subtype + ')'

      if (defaultValue === option.key) {
        html += '<option value="' + option.id + '" ' + dataTypes + ' selected>' + label + '</option>'
      } else {
        html += '<option value="' + option.id + '" ' + dataTypes + ' >' + label + '</option>'
      }
    }
  }

  return html
}