import { App } from '../../App'
import { lang } from '../../assets/lang'
import CONST_SOCKET from '../../const/socket'
import CONST_COMPONENT from '../../const/component'
import { showWarning } from '../../utils/functions'

export const handleConnectionLimitForRowsWhenConnectionCreated = (connection) => {
  const nodeInput = connection.input.node
  const nodeOutput = connection.output.node

  if (nodeInput.name === CONST_COMPONENT.ROW) {
    const targetNode = getListMessagesNodeFromSectionNode(nodeOutput)
    if (targetNode) {
      const key = targetNode.name + '-' + targetNode.id

      if (App.config.registryObject[key] > 0) {
        App.config.registryObject[key] += 1
      } else {
        App.config.registryObject[key] = 1
      }

      if (App.config.registryObject[key] > 10) {
        App.editor.removeNode(nodeInput)
        showWarning(lang.row_max_connection_limit)
      }
    }
  }
}

export const handleConnectionLimitForRowsWhenConnectionRemoved = (connection) => {
  const nodeInput = connection.input.node
  const nodeOutput = connection.output.node

  if (nodeInput.name === CONST_COMPONENT.ROW) {
    const targetNode = getListMessagesNodeFromSectionNode(nodeOutput)
    if (targetNode) {
      const key = targetNode.name + '-' + targetNode.id
      if (App.config.registryObject[key] > 0) {
        App.config.registryObject[key] -= 1
      }
    }
  }
}

const getListMessagesNodeFromSectionNode = (outputNode) => {
  if (outputNode.inputs.get(CONST_SOCKET.SECTION_INPUT) && 
    outputNode.inputs.get(CONST_SOCKET.SECTION_INPUT).connections[0]
  ) {
    const targetConnection = outputNode.inputs.get(CONST_SOCKET.SECTION_INPUT).connections[0];
    const targetNode = targetConnection.output.node
    
    return targetNode
  }

  return null
}