import Rete from 'rete'

import CONST_SOCKET from '../../const/socket'
import lingualName from '../../const/lingualName'
import CONST_COMPONENT from '../../const/component'
import SectionTemplate from './SectionTemplate'
import { lang } from '../../assets/lang'

export default class SectionComponent extends Rete.Component {
  constructor(config) {
    super(CONST_COMPONENT.SECTION)

    this.data.component = SectionTemplate
    this.socket = config ? config.sockets : null
  }

  builder (node) {
    node.lingualName = lingualName.SECTION
    this.setConnection()

    const sectionInput = new Rete.Input(CONST_SOCKET.SECTION_INPUT, lang.section_socket_input_reply, this.socket.sectionInput, false)
    const sectionOutputRows = new Rete.Output(CONST_SOCKET.SECTION_OUTPUT_ROWS, lang.section_socket_output_rows, this.socket.sectionOutputRows, true)

    return node
      .addInput(sectionInput)
      .addOutput(sectionOutputRows)
  }

  // eslint-disable-next-line
  worker (node, inputs, outputs) {}

  setConnection() {
    // Section may take connections from:
    this.socket.sectionInput.combineWith(this.socket.quickReplyOutput)

    // Section may be connected to:
    this.socket.sectionOutputRows.combineWith(this.socket.rowInput)
  }
}